/* App.css */
body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 800px;
  margin: 50px auto 20px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

h1 {
  color: #333;
}

p {
  font-size: 18px;
  color: #666;
}

button {
  padding: 10px 20px;
  margin: 10px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  color: white;
}

button.windows {
  background-color: #0078D6;
}

button.macos {
  background-color: #333;
}

button:hover {
  opacity: 0.8;
}

/* 新增的样式 */
.tutorial {
  padding: 10px;
  margin-left: 100px; /* 默认左边距 */
  text-align: left;
}

/* 针对较小屏幕的媒体查询，调整下半部分的布局 */
@media (max-width: 768px) {
  .tutorial {
    margin-left: 0; /* 手机上取消左边距，自动适应屏幕 */
    padding: 0 10px; /* 增加内边距，避免内容贴边 */
  }
}

.step {
  margin-bottom: 30px;
  font-size: 20px;
  font-weight: bold;
}

.step img {
  max-width: 100%;
  height: auto;
  display: block;
  margin-top: 10px;
}
